export default {"name":"鹿苑毛尖","img":"\\huangcha\\luwanmaojian\\img_20191122101744idqbam.jpg","desc":["远安鹿苑是黄茶的一种，产于湖北省远安县鹿苑寺一代。以揉捻后久堆焖黄的方式制作。色泽金黄，香气馥郁芬芳，汤色杏黄明亮，滋味醇厚甘凉。"],"menus":[{"name":"鹿苑毛尖茶叶历史","content":[{"text":"远古县古属峡州，唐代陆羽《茶经》中就有无安产茶这记载。据县志远载，鹿苑茶起初(公元1225)年为鹿苑增寺侧载值，产量甚微，当地村民见茶香味浓，便争于相引种，遂扩大到山前屋后种植，从而得以发展。现已在鹿苑一带创制出一种\n        黄茶\n        类的鹿苑毛尖。"},{"text":"鹿苑茶品质独具风格，芬芳馥郁，滋味醇厚，被誉为湖北茶中之佳品。"},{"text":"早在清代乾隆年间，就选为贡茶。"},{"text":"清代光绪九年(公元1883年)，高僧金田来到鹿苑巡寺讲法，品茶题诗，称颂鹿苑茶为绝品。诗云:“山精石液品超群，一种馨香满面熏，不但清心明目好，参禅能伏睡魔军。”"},{"src":"\\huangcha\\luwanmaojian\\img_201911221017449qck83.jpg"}]},{"name":"鹿苑毛尖茶叶特点","content":[{"text":"外形条索环状(环子脚)，白毫显露，色泽金黄(略带鱼子泡)，香郁高长，滋味醇厚回甘，汤色黄净明亮，叶底嫩黄匀整。"},{"text":"鹿苑茶品质独具风格，芬芳馥郁，滋味醇厚，被誉为湖北茶中之佳品。早在清代乾隆年间，就选为贡茶。相传乾隆皇帝饮后，顿觉清香扑鼻，精神倍振，饮食大增，即封鹿苑茶为“好淫茶”。清代光绪九年(公元1883年)，高僧金田来到鹿苑巡寺讲法，品茶题诗，称颂鹿苑茶为绝品。诗云：“山精石液品超群，一种馨香满面熏，不但清心明目好，参禅能伏睡魔军。”古今流传的“清漆寺的水(今湖北当阳县)，鹿苑寺的茶”，正是对鹿苑茶的赞语。"},{"src":"\\huangcha\\luwanmaojian\\img_20191122101744ope4yj.jpg"}]},{"name":"鹿苑毛尖加工工艺","content":[{"text":"鹿苑毛尖的鲜叶采摘时间在清明前后15天，采摘标准为一芽一、二叶，要求鲜叶细嫩、新鲜、匀齐、纯净，不带鱼叶、老叶、茶果。采回的鲜叶，先进行“短茶”，即将大的芽叶折短，选取一芽一叶初展芽尖，折下的单片、茶梗，另行炒制。习惯是上午采摘，下午短茶，晚间炒制。[4]"},{"text":"鹿苑毛尖的制造分杀青、二青、闷堆、拣剔、炒干五道工序。"},{"text":"一、杀青：锅温要求160℃左右，并掌握先高后低，每锅投叶量1～1.5公斤。炒时要快抖散气，抖闷结合，时间6分钟左右。炒至五、六成干起锅，趁热闷堆15分钟后散开摊放。"},{"text":"杀青"},{"text":"“杀青”一词来源先秦时代，人们在竹简上写字，但是竹简表面是油质的，不容易刻字，而且容易被虫蛀，所以就想出一个办法，就是把竹简放到火上烤，这道工序就叫“杀青”或“汗青”。后来到了秦代，人们用毛笔在竹青上写字，就免了刀刻这道工序，所以定稿的时候只需要削掉竹青，在竹白上写字就行了这一道手续也叫“杀青”。杀青就意味着定稿。现在人们也常用杀青指一部电影拍摄完成，比如：“一路转战甘肃、北京，艰苦拍摄了两个多月的《天下无贼》，昨天傍晚终于杀青”。"},{"text":"1.古代制竹简程序之一。将竹火炙去汗后，刮去青色表皮，以便书写和防蠹。《太平御览》卷六○六引 汉 刘向 《别录》：“杀青者，直治竹作简书之耳。新竹有汁，善朽蠹。凡作简者，皆於火上炙乾之。”《后汉书·吴佑传》：“ 恢 欲杀青简以写经书。” 李贤 注：“杀青者，以火炙简令汗，取其青易书，复不蠧，谓之杀青，亦谓汗简。”"},{"text":"2.古人校书，初书于竹简上，改定后再书于绢帛。后因泛称缮成定本或校刻付印为“杀青”。 南朝 梁武帝 《撰〈孔子正言〉竟述怀》诗：“删次起实沉，杀青在建酉。” 唐 刘知几 《史通·叙事》：“夫 班 马 执简，既五经之罪人，而 晋 宋 杀青，又三史之不若。” 梁启超 《新中国未来记》绪言：“既念欲俟全书卒业，始公诸世，恐更阅数年，杀青无日。”一说，古人著书，初稿书于青竹皮上，取其易于改抹，改定后再削去青皮，书于竹白，谓之“杀青”。参阅 明 姚福 《青溪暇笔》。"},{"text":"3.古时造纸方法。参阅 明 宋应星 《天工开物·杀青》。"},{"text":"4.\n        绿茶\n        加工制作的第一道工序，把摘下的嫩叶加高温，抑制发酵，使茶叶保持固有的绿色，同时减少叶中水分，使叶片变软，便于进一步加工。绿茶的加工分为为杀青、揉捻和干燥三个步骤，其中关键在于初制的第一道工序，即杀青。鲜叶通过杀青，酶的活性钝化，内含的各种化学成分，基本上是在没有酶影响的条件下，由热力作用进行物理化学变化，从而形成了绿茶的品质特征"},{"text":"5.影视剧制做。\"杀青\"一词现在多用于影视作品的制作上,经常会看到\"某某片于今日杀青\"的说法,这实际上是一部影视作品完成了前期的拍摄工作,开始步入到后期制作阶段的说法,和\"杀青\"一词是茶叶的初制过程中的关键步骤是非常有关联的."},{"text":"二、二青：炒二青锅温100℃左右，炒锅要磨光。投入湿坯叶1.5公斤左右，适当抖炒散气，并开始整形搓条，要轻搓、少搓，以防止产生黑条，时间约15分钟，当茶坯达七、八成干时出锅。"},{"text":"三、闷堆：闷堆是鹿苑毛尖品质特点形成的重要工序。茶坯堆积在竹盘内，拍紧压实，上盖湿布，闷堆5～6小时，促进黄变。"},{"text":"四、拣剔：主要剔除扁片、团块茶和花杂叶，以提高净度和匀度。"},{"text":"五、炒干：炒干温度80℃左右，投叶量2公斤左右，炒到茶条受热回松后，继续搓条整形，应用螺旋手势，闷炒为主，借以保持茶条环子脚的形成和色泽油润。约炒30分钟，达到足干后，起锅摊凉，包装贮藏。"},{"src":"\\huangcha\\luwanmaojian\\img_20191122101744x2kljt.jpg"}]},{"name":"鹿苑毛尖储藏方法","content":[{"text":"毛尖茶是\n        绿茶\n        的一种品种，其外形细直、圆润光滑，茶叶全身遍布着白毫，香气香远悠长，茶汤的味道十分的鲜浓甘爽独特，冲泡出来的茶汤颜色碧绿，茶叶舒张开来，慢慢沉入杯底，茶叶片片匀整，柔嫩鲜绿光滑。毛尖的色、香、味、形均有独特个性，其颜色鲜润、干净，不含杂质，香气高雅、清新，味道鲜爽、醇香、回甘。"},{"text":"1、毛尖茶能放多久"},{"text":"一般而言，毛尖茶的保质期在常温下一般为一年左右,不过影响茶叶品质的因素主要有温度、光线、湿度。如果存放方法得当，降低或消除这些因素，则可延长茶叶保质期。"},{"text":"判断茶叶是否过期，主要看它的味道与汤色。目前国家已经以食品卫生标准为准制定了茶叶标准，对保质期也作出了限定。一般来说，毛尖保质期一般为一年左右，但如果保存不当，受潮引起含水量超标的话可能两个月就过期了。开封的绿茶，应倒入密闭的容器内保存，最好在两个月以内喝完。"},{"text":"2、毛尖茶的保存法"},{"text":"普通密封保鲜法(也是家庭保鲜)：将买回的茶叶，立即分成若干小包，装于茶叶罐或筒里，最好一次装满并盖上盖，在不用时不要打开，用完把盖盖严。有条件可在茶叶罐内适当放些用布袋装好的生石灰，以起到吸潮和保鲜作用。"},{"text":"真空抽气充氮法：将茶叶装入铝箔与塑料做成的复合袋，采取一次性封闭真空抽气充氮包装贮存，也可适当加入些保鲜剂。一经启封后，最好在短时间内用完，否则时间一长，同样会陈化劣变。"},{"text":"冷库保鲜法：这种大量保鲜贮存法不加任何保鲜剂，纯属物理保鲜，只要库房建筑合理，库温保持在-4℃至2℃不变即可。但茶叶出库前必须要经过真空保鲜处理，否则，茶叶暴露空气中，由于外界冷热相遇，水分和氧气会形成水汽珠而凝结在茶叶上，加速茶叶劣变。"}]},{"name":"鹿苑毛尖冲泡方法","content":[{"text":"冲泡鹿苑毛尖，第一杯当倒掉，喝第二杯、第三杯时，其香味即沁入心脾。另外，如用正宗紫砂\n        茶具\n        冲泡鹿苑毛尖，品时啜会更得其味。"},{"text":"紫砂茶具"},{"text":"紫砂茶具，由陶器发展而成，是一种新质陶器。它始于宋代，盛于明清，流传至今。北宋梅尧臣的《依韵和杜相公谢蔡君谟寄茶》中说道:“小石冷泉留早味，紫泥新品泛春华。”说的是紫砂茶具在北宋刚开始兴起的情景。至於紫砂茶具由何人所创，已无从考证。。但从确切有文字记载而言，紫砂茶具则创造於明代正德年间。"},{"text":"今天紫砂茶具是用江苏宜兴南部及其毗邻的浙江长兴北部埋藏的一种特殊陶土，即紫金泥烧制而成的。这种陶土，含铁量大，有良好的可塑性，烧制温度以摄氏1150度左右为宜。紫砂茶具的色泽， 可利用紫泥泽和质地的差别， 经过“澄”、“洗”，使之出现不同的色彩，如可使天青泥呈暗肝色，蜜泥呈淡赭石色，石黄泥呈朱砂色，梨皮泥呈冻梨色等;另外，还可通过不同质地紫泥的调配，使之呈现古铜、淡墨等色。优质的原料，然的色泽，为烧制优良紫砂茶具奠定了物质基础。"},{"text":"宜兴紫砂茶具之所以受到茶人的锺情，除了这种茶具风格多样，造型多变，富含文化品位，以致在古代茶具世界中别具一格外，还与这种茶具的质地适合泡茶有关。后人称紫砂茶具有三大特点，就是“泡茶不走味，贮茶不变色，盛暑不易馊。”"},{"text":"紫砂茶具属陶器茶具的一种。它坯质致密坚硬，取天然泥色，大多为紫砂，亦有红砂、白砂。成陶火度在1100—1200摄氏度，无吸水性，音粗韵长。它耐寒耐热，泡茶无熟汤味，能保真香，且传热缓慢，不易烫手，用它炖茶，也不会爆裂。因此，历史上曾有“一壶重不数两，价重每一二十金，能使土与黄金争价”之说。但美中不足的是受色泽限制，用它较难欣赏到茶叶的美姿和汤色。"},{"text":"目前我国的紫砂茶具，质量以产於江苏宜兴的为最，与其毗邻的浙江长兴亦有生产。经过历代茶人的不断创新，“方非一式，圆不相同”就是人们对紫砂茶具器形的赞美。一般认为，一件姣好的紫砂茶具，必须具有三美，即造型美、制作美和功能美，三者兼备方称得上是一件完善之作。"},{"text":"如果以时间来划分的话，\n        紫砂壶\n        大致可以分为明清以前的古壶，和二十世纪以后的近当代壶。1950年至1960年的宜兴紫砂壶，是其中一个具有明显时代特征的品种，这个时期的紫砂工艺，可以说是紫砂制造工艺能够流传至今、发扬光大的一个重要原因。二十世纪五十年代初期，历经战乱的宜兴紫砂从业者仅存五十多人，而制壶的老艺人更只剩下二十多人。在人民政府的扶持下，老艺人组织起了合作社，恢复了紫砂壶的生产。这个时期出现了朱可心、王寅春、蒋蓉、顾景舟、吴云根、裴石民等一批壶艺大师，这些大师的作品，精品迭出，砂质优良，工艺规范，受到了壶具收藏爱好者的追捧与青睐。"},{"text":"宜兴紫砂壶，始于明代正德年间。从此蔚然成风。宜兴紫砂壶是艺术品，它形制优美，颜色古雅。 另一方面，它又是实用品，明清两代，伴随着瓷器的兴盛，宜兴紫砂器艺术也突飞猛进地发展起来。邵友兰，清道光年间人，是当时的制壶名家之一。也是当代紫砂大师顾景舟先生祖母的前辈人。邵友兰善做仿古器型，尤以“配泥精致，质坚如玉为一绝”。所用印章有椭圆式带边纹的“阳羡邵友兰制”，还有带边的方章“友兰秘制”，小印楷书“友兰”二字，铭刻一般署名“二泉”所作。由于紫砂艺人的印章材料坚固，一般可以沿用若干年，甚至终生都在使用，因此印章款的鉴别是判断紫砂壶作者的一个直接依据。"}]},{"name":"鹿苑毛尖功效作用","content":[{"text":"1、保护脾胃，提高食欲，帮助消化。 由于\n        黄茶\n        是沤茶，在沤的过程中，会产生大量的消化酶，对脾胃最有好处，消化不良，食欲不振、懒动肥胖、都可饮而化之。"},{"text":"2、 能穿入脂肪细胞，使脂肪细胞在消化酶的作用下恢复代谢功能，将脂肪化除 。"},{"text":"3、富含茶多酚、氨基酸、可溶糖、维生素等丰富营养物质，对防治食道癌有明显功效。此外，黄茶鲜叶中天然物质保留有85%以上，而这些物质对防癌、抗癌、杀菌、消炎均有特殊效果，为其他茶叶所不及。"}]},{"name":"鹿苑毛尖历史故事","content":[{"text":"远古县古属峡州，唐代陆羽《茶经》中就有无安产茶这记载。据县志远载，鹿苑茶起初(公元1225)年为鹿苑增寺侧载值，产量甚微，当地村民见茶香味浓，便争于相引种，遂扩大到山前屋后种植，从而得以发展。现已在鹿苑一带创制出一种\n        黄茶\n        类的鹿苑毛尖。"}]},{"name":"鹿苑毛尖鉴别好茶","content":[{"text":"鹿苑毛尖略带鱼子泡的颜色，条索呈现环状，白毫很明显，通体橙黄且带有金光，因此很容易识别出来。假冒茶叶的茶形会有明显差异，弯曲但是极少能够呈现接环状。"},{"text":"冲泡后，鹿苑毛尖香气扑鼻，汤色为黄色，叶底嫩黄匀称，没有漂浮的絮状物，也没有沉淀物。假冒茶叶冲泡后，茶汤中浮渣很多，茶汤混浊不透明，给人感觉“很脏”。"}]}]}